@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300italic,600,600italic,700);
.navbar{
  width: 100%;
 background-color: var(--cp2color1);
 position: fixed;
 top: 0;
 z-index: 3;
}

.navbar .nav-container{
  max-width:960px ;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  justify-content:center;
  align-items: center;
  
}

.navbar .nav-container h1{
  color: var(--fontWhite);
  font-size: 2rem;
  font-weight: 100;
  padding:   10px 0;
  letter-spacing: 3px;
}
@media(max-width:500px){
  .navbar .nav-container h1{
    font-size: 1.6rem;
  }
}
.navbar .nav-container .main-link {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar .nav-container .main-link li,
.show-link li{
  margin: 0 2px;
  padding:  1rem 10px;
  color: var(--fontWhite);
  
}
@media(max-width:800px){
  .show-link li{
    margin: 0 2px;
    padding:10px 1rem;
    
  }
}
.navbar .nav-container .main-link li a,
.show-link li a{
  color: var(--fontWhite);
  font-size: 17px;
  padding-bottom: 2px;
  border-bottom: 2px solid transparent;
  transition: var(--transition);
  letter-spacing: 3px;
  cursor: pointer;
  
}
.navbar .nav-container .main-link li a:hover,
.show-link li a:hover{
  
  border-bottom: 2px solid var(--buttonBorder);
}
.active{
  border-bottom: 2px solid var(--buttonBorder) !important;
}
.nav-container .mobile-menu{
  padding:.6rem 0 ;
  display: none;
}

.display-link{
  display: block !important;
}
@media(max-width:768px){
  .navbar .nav-container{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    
  }
  .nav-container .main-link-container{
    display: none;
  }
  .nav-container .main-link{
    width: 100%;
    flex-direction: column;
    align-items: flex-start !important;
    
  }
  .navbar .nav-container .main-link li{
    padding: 10px ;
  }
  .nav-container .mobile-menu{
    margin-left: auto;
    display: block;
    
  }
  
}
.show-link{
  width: 100%;
 background-color: var(--cp2color1);
 transition: var(--transition);
  
}






.header-container{
  position: relative;
  width: 100%;
  height: calc(100vh - 57px);
  background-image:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),
   url(/react-protfolio-project/static/media/ferenc-almasi-eYpcLDXHVb0-unsplash.138bbd73.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.header-container .header-info{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 2;
}
.header-container .header-info button{
  border: 1px solid var(--buttonBorder);
  padding: 8px 1rem;
  background-color: transparent;
  border-radius: 5px;
  letter-spacing: 3px;
  transition: var(--transition);
  margin-top: 1rem;
  text-transform: uppercase;
  outline-style: none;
  cursor: pointer;
}
.header-container .header-info button:hover{
  background-color: var(--buttonHover);
}
.header-container .header-info button a{
 color: var(--fontWhite);
}
 .header-container .header-info .typed-container .typed-text{
  font-size: 2rem;
  color: var(--fontWhite);
  letter-spacing: 3px;
}

@media(max-width:500px){
  .header-container .header-info .typed-container .typed-text{
    font-size: 1.5rem;
    
  }
}
.tsparticles-canvas-el{
  position: absolute;
  height: 100vh;
  width: 100%;
  z-index: 1;
}
.aboutMe-wraper{
  background-color: var(--light-gray2);
}
.aboutMe-container .aboutMe-info{
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(350px,1fr));
  grid-auto-rows: auto;
  justify-content: center;
  align-content: center;
  grid-gap: 2rem 1rem ;
  
}
.aboutMe-container .aboutMe-info h1{
  font-weight: 300;
  font-size: 2.5rem;
  
}
@media(max-width:900px){
  .aboutMe-container .aboutMe-info h1{
    font-size: 2rem;
    
  }
}
.aboutMe-container .aboutMe-info h1 span{
  font-weight: 700;
  
}

.aboutMe-container .aboutMe-info h1,
.aboutMe-container .profile-info .sub-heading{
  margin-bottom: 1rem;

}
.aboutMe-container .profile-info .sub-heading{
  font-size: 1.2rem;
  font-weight: 600;

}
.aboutMe-container .aboutMe-info .profile-pic,
.aboutMe-container .aboutMe-info .profile-info{
  width: 100%;
  max-width: 500px;
  height: auto;
  margin: 0 auto;
  
}

.aboutMe-container .aboutMe-info .profile-pic {
  width:250px;
  height: 250px;
  
}
@media(max-width:500px){
  .aboutMe-container .aboutMe-info .profile-pic {
    width:160px;
    height: 160px;
    
  }
}
.aboutMe-container .aboutMe-info .profile-pic img{
  width:100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  border: 1px solid var(--buttonBorder);
  padding: 5px;
}

.skills-wraper{
  /* background-color: #fafafa; */
  background-color: var(--primary-gray);
}
.skills-container .skills-info{
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(150px,1fr));
  grid-auto-rows: auto;
  justify-content: center;
  align-content: center;
  grid-gap: 2rem 1rem ;
  
  
}
.skills-container  h1{
  font-weight: 300;
  font-size: 2.5rem;
  text-align: center;
  padding-bottom: 3rem;
  
  
}
@media(max-width:500px){
  .skills-container  h1{
    font-size: 1.5rem;
    
  }
}

.skills-container .skills-info img{
  width:100%;
  max-width: 200px;
  height: 150px;
  object-fit: cover;
  border-radius: 5px;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5);
  padding: 1rem;
  margin: 0 auto;
}

.project-wraper{
  /* background-color: #fafafa; */
  background-color: var(--light-gray2);
}
.project-container .project-info{
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(220px,1fr));
  grid-auto-rows: auto;
  justify-content: center;
  align-content: center;
  grid-gap: 2rem 1rem ;
  
}
.project-container  h1{
  font-weight: 300;
  font-size: 2.5rem;
  text-align: center;
  padding-bottom: 3rem;
  font-weight: 300;
  
}
@media(max-width:500px){
  .project-container  h1{
    font-size: 1.5rem;
    
  }
}

.project-container .project-info .single-project-info{
  width:100%;
  max-width: 250px;
  height: auto;
  border-radius: 5px;
  box-shadow: 0 0 2px 0 rgba(0,0,0,.5);
  padding: 1rem;
  margin: 0 auto;
  position: relative;
}
.project-container .project-info .single-project-info img{
  width:100%;
  height: 180px;
  object-fit: cover;
 
  
}

.project-container .project-info .single-project-info .overlay{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  background-color: rgba(0,0,0,.5);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: var(--transition);
  border-radius: 5px;
}
.project-container .project-info .single-project-info .overlay:hover{
  opacity: 1;
}
.project-container .project-info .single-project-info .overlay .search-icon{
  font-size: 1.5rem;
  color: var(--fontWhite);
  z-index: 2;
  cursor: pointer;
}


.Modal {
  width: 100%;
 max-width: 450px;
 height: auto;
 padding: 1rem;
  background-color: var(--fontWhite);
  position: relative;
  border: none;
  outline-style: none;
  border-radius: 5px;
}
.Modal button{
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  outline-style: none;
  background-color: transparent;
  cursor: pointer;
}
.Modal button .icon-close{
  font-size: 1.5rem;
}
/* .Modal h2{
  font-weight: 300;
} */
.Modal h2,
.Modal img,
.Modal p,
.Modal b{
  margin-bottom: 10px;
}
.Modal img{
  width: 100%;
  max-height: 300px;
  object-fit: cover;
  border-radius: 5px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5);
}
@media(max-height:500px){
  .Modal img{
    width: 100%;
    max-height: 100px;
    object-fit: cover;
  }
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color:rgba(0,0,0,.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 4;
  padding: 1rem;
  overflow: scroll;
}
.contract-raper{
  background-color: var(--background-dark);
  color: #999;

}
.contract-section {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.contract-section h1{
  color: var(--fontWhite);
  text-align: center;
  font-weight: 300;
  font-size: 2rem;
  margin-bottom: 2rem;
}
@media(max-width:500px){
  .contract-section h1{
    font-size: 1.5rem;
  }
}
.contract-section form{
  width: 100%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  
}
.contract-section form .single-input{
 
  display: flex;
  flex-direction: column;
  
}
.contract-section form .inline-form{
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 1rem;
  
}
@media(max-width:600px){
  .contract-section form .inline-form{
    grid-template-columns: 1fr;
    
  }
}
.contract-section form textarea,
.contract-section form input{
  margin: 10px 0;
  padding:  10px;
  border: none;
  background-color: #282828;
  border-radius: 5px;
  font-size: 1rem;
  color: var(--fontWhite);
  outline-style: none;
  font-family: 'Open Sans', sans-serif;
  
  
}
.contract-section form textarea:focus,
.contract-section form input:focus{
  background-color: #252525;
  
}
.contract-section form textarea::-webkit-input-placeholder{
  font-size: 1rem;
  
}
.contract-section form textarea:-ms-input-placeholder{
  font-size: 1rem;
  
}
.contract-section form textarea::placeholder{
  font-size: 1rem;
  
}

.contract-section form button{
  font-family: 'Open Sans', sans-serif;
  border: 1px solid var(--buttonBorder);
  padding: 8px 1rem;
  background-color: transparent;
  border-radius: 5px;
  letter-spacing: 3px;
  transition: var(--transition);
  margin-top: 1rem;
  text-transform: uppercase;
  color: var(--fontWhite);
  outline-style: none;
}
.contract-section form button:hover{
  background-color: var(--buttonHover);
}

.valid{
  color: rgba(39, 174, 96,1.0);
}
.unvalid{
  color: rgba(192, 57, 43,1.0);
}
.wraper-footer{
  background-color:var(--fontColor);
  color: var(--primary-gray2);
}
.footer-container{
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer-container .copy-right{
  color: var(--primary-gray2);
  font-size: 1.1rem;
}
.footer-container .line{
  height: 100%;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}
.footer-container .line p{
  height: 100%;
  border-left: 1px solid var(--primary-gray2);
  border-bottom: none !important;
  
}
@media(max-width:600px){
  .footer-container .line p{
    width: 200px;
    border-left: none !important;
    border-bottom: 1px solid var(--primary-gray2);
    
  }
}
.footer-container .info-container{
  width: 100%;
  margin: 0 auto 1.5rem auto;
  display: grid;
  grid-template-columns: repeat(5,auto);
  grid-gap: 1rem .5rem;
  
}
@media(max-width:600px){
  .footer-container .info-container{
    grid-template-columns: 1fr;
    
  }
}
.footer-container .info-container .footer-section{
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
.footer-container .info-container .office-info a{
  color: var(--primary-gray2);
  transform: var(--transition);
}
.footer-container .info-container .office-info a:hover{
  color: var(--fontWhite) ;
  
}
.footer-container .info-container .sections-links a{
  color: var(--buttonBorder);
  transform: var(--transition);
  cursor: pointer;
}
.footer-container .info-container .sections-links a:hover{
  color: var(--buttonHover);
  
}
.footer-container .info-container  .social-icon{
  
  display: flex;
  flex-direction: row ;
  align-items: flex-start;
  justify-content: center;
  
}
.footer-container .info-container  .sections-links{
  
  display: flex;
  flex-direction: row ;
  align-items: flex-start;
  justify-content: center;
}
.footer-container .info-container  .sections-links div{
  margin: 0 10px;
  display: flex;
  flex-direction: row ;
  flex-direction: column;
}
.footer-container .info-container  .social-icon button{
  
  outline-style: none;
border: none;
  margin: 0 5px;
  
}


